  display: -webkit-flex;
  display: flex;
  width: 9.466666666666667rem;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  margin: 0.346666666666667rem 0.266666666666667rem 0 0.266666666666667rem;
  border-bottom: 0.04rem solid #f7f7f7;
